<template>
  <div class="baseTemplateBtn">
    <el-button
      type="success"
      size="small"
      plain
      @click="dialogVisiable = !dialogVisiable"
    >
      
    </el-button>
    <el-dialog
      title=""
      :visible.sync="dialogVisiable"
      width="80%"
      :close-on-click-modal="false"
    >
      <div class="rules">
        <div>
          <h3></h3>
          <p>1</p>
          <p>
            2
          </p>
        </div>
        <div style="margin-left: auto">
          <el-button type="primary" @click="onExport"></el-button>
        </div>
      </div>
      <table class="table" border>
        <tr>
          <th v-for="item in table.column" :key="item">{{ item }}</th>
        </tr>
        <tr v-for="(i, index) in table.data" :key="index">
          <td v-for="item in table.column" :key="item" class="td">
            {{ i[item] }}
          </td>
        </tr>
      </table>
    </el-dialog>
  </div>
</template>

